import React from "react"
import "../css/MainContent.css"
import Profile from "../img/myAvatar.svg"


function MainContent(){
    return(
        <div className="mainContent">
            <h1 className="mainContent__hello">Hello My Name is Daniel and I am Junior Frontend developer at least I try to be and this is my portfolio created with React</h1>
            <p className="mainContent__emailText">If you like my work, email me</p>
            <img className="mainContent__image" src={Profile} alt="profile"></img>
            
        </div>
    )
}

export default MainContent